<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Pay By Credit Card">
			<router-link :to="{name: 'PayShipping'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

	<div class="page-body">
		<h3>Pay By Credit Card</h3>
		<c-picker field-label="Card Type" :field-value="editCard.CreditCardType" 
			:picker-list="cardTypeList" picker-value-key="CardTypeName" @selectedChanged="onCardTypeChanged">
		</c-picker>

		<mt-field label="Credit Card Number" type="number" v-model="editCard.CreditCardNumber"></mt-field>
		<mt-field label="Security Code" type="text" v-model="editCard.CVV2"></mt-field>

		<c-picker-number field-label="Exp Month" :field-value="editCard.ExpMonth" 
			:picker-list="expirationMonthList" picker-value-key="Month" @selectedChanged="onExpMonthChanged">
		</c-picker-number>

		<c-picker-number field-label="Exp Year" :field-value="editCard.ExpYear" 
			:picker-list="expirationYearList" picker-value-key="Year" @selectedChanged="onExpYearChanged">
		</c-picker-number>

		<h3>Billing Address</h3>
		<mt-field label="BillName" type="text" v-model="editForm.BillName"></mt-field>
		<mt-field label="BillAddress1" type="text" v-model="editForm.BillAddress1"></mt-field>
		<mt-field label="BillAddress2" type="text" v-model="editForm.BillAddress2"></mt-field>
		<mt-field label="BillCity" type="text" v-model="editForm.BillCity"></mt-field>
		<mt-field label="BillState" type="text" v-model="editForm.BillState"></mt-field>
		<mt-field label="BillZipcode" type="text" v-model="editForm.BillZipcode"></mt-field>
	
		<div>
			<mt-button @click.native="editItemCancel"> &#60;&#60; Shipping</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Finalize &#62;&#62;</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import Order from '../api/Order.js';

import ComboPicker from '../components/ComboPicker.vue';
import NumberPicker from '../components/NumberPicker.vue';

export default {
	name: 'PayBilling',
	components:{
		'c-picker': ComboPicker,
		'c-picker-number': NumberPicker
	},
	data(){
		return {
			editCard: {},
			editForm: {},
			cardTypeList:[],
			expirationMonthList: [],
			expirationYearList:[]
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		onCardTypeChanged(obj)
		{
			this.editCard.CreditCardType = obj.CardTypeName;
		},
		onExpMonthChanged(obj)
		{
			this.editCard.ExpMonth = obj.Month;
		},
		onExpYearChanged(obj)
		{
			this.editCard.ExpYear = obj.Year;
		},
		saveOrder(entity) 
		{	
			Indicator.open();

			entity.IsDirty = true;
			entity.IsNew = false;
			entity.IsDeleted = false;

			let customer = Utils.getCustomer();
			entity.ModifiedBy = customer.Email;

			Order.Save(entity).then((res) => {
				Indicator.close();
				if(res.data.OK){
					this.$store.commit('setCart', entity);	
					this.$router.push({ name: 'PayConfirm' });		
				}
				else
				{
					this.$message({message: res.data.Error,type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		},
		editItemCancel()
		{
			this.$store.commit('setCreditCard', {});
			this.$router.push({ name: 'PayShipping' });
		},
		editItemSubmit() 
		{
			this.$store.commit('setCreditCard', this.editCard);
		
			let entity = Object.assign({}, this.editForm);
			this.saveOrder(entity);
		}
	},
	created()
	{
		let currentTime = new Date();

		let card = this.$store.state.creditCard;
		if(Object.keys(card).length == 0)
		{
			card.CreditCardType = 'Visa';

			
			card.ExpMonth = currentTime.getMonth();
			card.ExpYear = currentTime.getFullYear();
		}
		this.editCard = Object.assign({}, card);

		//CC setting
		this.cardTypeList.push({CardTypeName: 'Visa'});
		this.cardTypeList.push({CardTypeName: 'MasterCard'});
		this.cardTypeList.push({CardTypeName: 'Discover'});
		this.cardTypeList.push({CardTypeName: 'Amex'});

		let currentYear = currentTime.getFullYear();
		for(let i = currentYear; i < currentYear + 10; i++)
		{
			this.expirationYearList.push({Year: i});
		}

		for(let i = 1; i <= 12; i++)
		{
			this.expirationMonthList.push({Month: i});
		}
	},
	mounted()
	{
		let entity = this.$store.state.cart;
		if(Object.keys(entity).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.editForm = Object.assign({}, entity);
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
