<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Pay Success">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<p><strong>Congratulations!</strong> Your order has been placed successfully!</p>
		<p>Your Order ID: {{order.OrderNumber}}</p>
		<p>Your Transaction ID: {{paymentResult.TransID}}</p>
		<p>Total Amout: {{paymentResult.Amount}}</p>
		<p><small>*Please use the Order ID when inquiring about order status</small></p>

		<table>
			<tr>
				<td>DATAWATCH SYSTEMS INC</td>
			</tr>
			<tr>
				<td>4401 EAST WEST HIGHWAY, #500</td>
			</tr>
			<tr>
				<td>BETHESDA,  MD 20814</td>
			</tr>
		</table>

		<c-order :order="order" :paid-type="paidType" :paid-number="paidNumber"></c-order>

		<div class="hide_print">
			<mt-button type="primary" @click.native="printClick">Print</mt-button>
			<mt-button @click.native="cancelClick">Home</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import OrderDetail from '../components/OrderDetail.vue';

export default {
	name: 'PaySuccess',
	data(){
		return {
			order: {},
			paidType: '',
			paidNumber: '',
			paymentResult: {}
		}
	},
	components:{
		'c-order' : OrderDetail
	},
	methods:{
		printClick()
		{
			window.print(); 
			return false;
		},
		cancelClick()
		{
			this.$router.push({ name: 'Dashboard' });
		}
	},
	created()
	{
		//Cart
		let entity = this.$store.state.cart;
		if(Object.keys(entity).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.order = Object.assign({}, entity);

        //Payment
		let payResult = this.$store.state.paymentResult;
		if(Object.keys(payResult).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.paymentResult = Object.assign({}, payResult);

		//CreditCard
		let card = this.$store.state.creditCard;
		if(Object.keys(card).length == 0)
		{
			this.paidType = 'PO Number';
			this.paidNumber = this.order.PONumber;
		}
		else
		{
			this.paidType = card.CreditCardType;
			this.paidNumber = card.CreditCardNumber;
		}
		
		//Clear
        this.$store.commit('setCart', {});
		this.$store.commit('setCreditCard', {});
		this.$store.commit('setPaymentResult', {});
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
	@media print {
		.hide_print
		{
			display: none;
		}
	}
</style>
