<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Pay Confirm">
			<router-link :to="{name: 'Cart'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

	<div class="page-body">
		<p>
			Note:  Your Order is Not Complete; Please click the "Place Your Order" button on the bottom of the screen to complete your order, thank you.
		</p>

		<c-order :order="order" :paid-type="paidType" :paid-number="paidNumber"></c-order>

		<div>
			<mt-button @click.native="cancelClick"> &#60;&#60; Billing</mt-button>
			<mt-button type="primary" @click.native="payClick">Place Your Order</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import Order from '../api/Order.js';
import BizOrder from '../api/BizOrder.js';
import OrderDetail from '../components/OrderDetail.vue';

export default {
	name: 'PayConfirm',
	data(){
		return {
			order: {},
			paidType: '',
			paidNumber: ''
		}
	},
	components:{
		'c-order' : OrderDetail
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		cancelClick()
		{
			let card = this.$store.state.creditCard;
			if(Object.keys(card).length == 0)
			{
				this.$router.push({ name: 'PayMonthly' });
			}
			else
			{
				this.$router.push({ name: 'PayBilling' });
			}
		},
		payClick() 
		{
			Indicator.open();
			
			this.order.IsDirty = true;
			this.order.IsNew = false;
			this.order.IsDeleted = false;
			this.order.OrderVia = 'Web';

			let customer = Utils.getCustomer();
			this.order.ModifiedBy = customer.Email;

			let card = this.$store.state.creditCard;
			if(Object.keys(card).length == 0)
			{
				let poTrans ={};
                poTrans.PONumber = this.order.PONumber;
				BizOrder.PlaceOrder(this.order, poTrans).then((res) => {
					Indicator.close();
					if(res.data.OK){	
						this.$store.commit('setPaymentResult', res.data);
						this.$router.push({ name: 'PaySuccess' });	
					}
					else
					{
						this.$message({message: res.data.ErrorText,type: 'error'});
						console.log(res.data);
					}
				}).catch(err=>{
					Indicator.close();
					this.showNetworkError();
					console.log(err);
				});
			}
			else
			{
				let ccTrans = {};
				ccTrans.CardNumber = card.CreditCardNumber;
				ccTrans.CVV = card.CVV2;
				ccTrans.ExpYear = card.ExpYear;
				ccTrans.ExpMonth = card.ExpMonth;
				BizOrder.PlaceOrder(this.order, ccTrans).then((res) => {
					Indicator.close();
					if(res.data.OK){	
						this.$store.commit('setPaymentResult', res.data);
						this.$router.push({ name: 'PayConfirm' });		
					}
					else
					{
						this.$message({message: res.data.Error,type: 'error'});
						console.log(res.data);
					}
				}).catch(err=>{
					Indicator.close();
					this.showNetworkError();
					console.log(err);
				});
			}	
		}
	},
	created()
	{
		let entity = this.$store.state.cart;
		if(Object.keys(entity).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.order = Object.assign({}, entity);

		//Payment
		let card = this.$store.state.creditCard;
		if(Object.keys(card).length == 0)
		{
			this.paidType = 'PO Number';
			this.paidNumber = this.order.PONumber;
		}
		else
		{
			this.paidType = card.CreditCardType;
			this.paidNumber = card.CreditCardNumber;
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
