<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Pay By PO">
			<router-link :to="{name: 'PayShipping'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<h3>Shipping Address</h3>
		<mt-field label="PO Number" type="number" v-model="editForm.PONumber"></mt-field>
		<p>
			* If you do not have a PO System, a recommended substitute is to use today's date as a PO Number (Example:  APR222010 or 04222010).
		</p>

		<h3>Your selected Billing Address</h3>
		<div>
			<mt-field label="BillTo" type="text" v-model="editForm.BillTo"></mt-field>
			<mt-field label="BillName" type="text" v-model="editForm.BillName"></mt-field>
			<mt-field label="BillCompany" type="text" v-model="editForm.BillCompany"></mt-field>
			<mt-field label="BillAddress1" type="text" v-model="editForm.BillAddress1"></mt-field>
			<mt-field label="BillAddress2" type="text" v-model="editForm.BillAddress2"></mt-field>
			<mt-field label="BillCity" type="text" v-model="editForm.BillCity"></mt-field>
			<mt-field label="BillState" type="text" v-model="editForm.BillState"></mt-field>
			<mt-field label="BillZipcode" type="text" v-model="editForm.BillZipcode"></mt-field>
		</div>

		<div>
			<mt-button @click.native="editItemCancel"> &#60;&#60; Shipping</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Finalize &#62;&#62;</mt-button>
			<mt-button @click.native="billingAddressVisible=true">Billing Address List</mt-button>
		</div>

		<mt-popup v-model="billingAddressVisible" style="width:100%;height:100%;overflow:auto;">
			<h3>Billing Address List</h3>
			<div v-for="item in billingAddressList" :key="item.BillingAddressPK">
				<mt-cell>
					<span slot="title">
						<span class="ContactName">{{item.ContactName}}</span>
						<span class="CompanyName">{{item.CompanyName}}</span>
						<span class="CompositeAddress">{{item.CompositeAddress}}</span>
					</span>
					<mt-button icon="more" @click="useAddressClick(item)"></mt-button>
				</mt-cell>
			</div>
		</mt-popup>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import axios from 'axios';
import Utils from '../utils/index.js';
import Order from '../api/Order.js';
import Customer from '../api/Customer.js';
import BillingAddress from '../api/BillingAddress.js';

export default {
	name: 'PayMonthly',
	data(){
		return {
			editForm: {},
			billingAddressList:[],
			billingAddressVisible: false
		}
	},
	methods:{
	    $message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		$alert(message, title)
		{
			MessageBox.alert(message, title, {confirmButtonText: 'OK'});
		},	
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		editItemCancel()
		{
			this.$router.push({ name: 'PayShipping' });
		},
		editItemSubmit() 
		{
			if(this.editForm.PONumber == '')
			{
				this.$alert('The PO Number can not be empty.','Warning');
				return;
			}

			Indicator.open();
			let entity = Object.assign({}, this.editForm);
			entity.IsDirty = true;
			entity.IsNew = false;
			entity.IsDeleted = false;

			let customer = Utils.getCustomer();
			entity.ModifiedBy = customer.Email;

			Order.Save(entity).then((res) => {
				Indicator.close();
				if(res.data.OK){	
					this.$store.commit('setCart', entity);
					this.$store.commit('setCreditCard', {});
					this.$router.push({ name: 'PayConfirm' });		
				}
				else
				{
					this.$message({message: res.data.Error,type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		},
		useAddressClick(row)
		{
			let warning = 'Are you sure this is the correct Bill To address?';
			if(row.ContactName != '')
			{
				warning += '<br />' + row.ContactName;
			}
			if(row.CompanyName != '')
			{
				warning += '<br />' + row.CompanyName;
			}
			if(row.Address != '')
			{
				warning += '<br />' + row.Address;
			}
			if(row.Address2 != '')
			{
				warning += '<br />' + row.Address2;
			}
			if(row.City != '' && row.State != '' && row.Zipcode != '')
			{
				warning += '<br />' + row.City + ' ' + row.State + ' ' + row.Zipcode;
			}
				
		    MessageBox.confirm(warning, 'Warning', {confirmButtonText: 'OK', cancelButtonText: 'Cancel'}).then((action) => {
				this.editForm.BillTo = row.BillTo;
				this.editForm.BillName = row.ContactName;
				this.editForm.BillCompany = row.CompanyName;
				this.editForm.BillAddress1 = row.Address;
				this.editForm.BillAddress2 = row.Address2;
				this.editForm.BillCity = row.City;
				this.editForm.BillState = row.State;
				this.editForm.BillZipcode = row.Zipcode;
				this.billingAddressVisible = false;
			}).catch(err=>
			{
				this.billingAddressVisible = false;
			});;
		}
	},
	mounted()
	{
		let entity = this.$store.state.cart;
		if(Object.keys(entity).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.editForm = Object.assign({}, entity);

		//billing Address
		let customer = Utils.getCustomer();
		this.billingAddressList = this.$store.state.billingAddressList;
		if(this.billingAddressList.length == 0)
		{
			axios.all([BillingAddress.GetByCustomer(customer.CustomerPK), Customer.GetByPK(customer.CustomerPK)])
				.then(axios.spread((resList, resItem)=>{
					this.billingAddressList = resList.data;
					if(resItem != null)
					{
						let first = {};
						first.BillingAddressPK = '00000000-0000-0000-0000-000000000000';
						first.BillTo = 0;
						first.ContactName = resItem.data.BillContactName;
						first.CompanyName = resItem.data.BillCompanyName;
						first.Address = resItem.data.BillAddress;
						first.Address2 = resItem.data.BillAddress2;
						first.City = resItem.data.BillCity;
						first.State = resItem.data.BillState;
						first.Zipcode = resItem.data.BillZipcode;
						this.billingAddressList.splice(0, 0, first);
					}

					for(let i = 0; i < this.billingAddressList.length; i++)
					{
						let item = this.billingAddressList[i];
						item.CompositeAddress = item.Address + ' ' + item.Address2 + ' ' + item.City + ', ' + item.State + ' ' + item.Zipcode;
					}

					this.$store.commit('setBillingAddressList', this.billingAddressList);
				}));
		}
		
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
	.ContactName {
		width: 20%;
		display: inline-block;
	}
	.CompanyName {
		width: 20%;
		display: inline-block;
	}
	.CompositeAddress {
		width: 50%;
		display: inline-block;
		text-align: right;
	}
</style>
