<template>
<div>

	<div class="page-header">
		<mt-header fixed title="Shipping">
			<router-link :to="{name: 'Cart'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>


	<div class="page-body">
		<h3>Shipping Address</h3>
		<div>
			<mt-field label="ShipName" type="text" v-model="editForm.ShipName"></mt-field>
			<mt-field label="ShipCompany" type="text" v-model="editForm.ShipCompany"></mt-field>
			<mt-field label="ShipAddress1" type="text" v-model="editForm.ShipAddress1"></mt-field>
			<mt-field label="ShipAddress2" type="text" v-model="editForm.ShipAddress2"></mt-field>
			<mt-field label="ShipCity" type="text" v-model="editForm.ShipCity"></mt-field>

			<c-picker field-label="State" :field-value="editForm.ShipState" 
				:picker-list="stateList" picker-value-key="ShortName" @selectedChanged="onStateChanged">
			</c-picker>

			<c-picker field-label="County" :field-value="editForm.ShipCounty" 
				:picker-list="countyList" picker-value-key="county" @selectedChanged="onCountyChanged">
			</c-picker>

			<mt-field label="ShipZipcode" type="text" v-model="editForm.ShipZipcode" @blur="changeCounty"></mt-field>
			<mt-field label="ShipPhone" type="text" v-model="editForm.ShipPhone"></mt-field>
			<mt-field label="ShipEmail" type="email" v-model="editForm.ShipEmail"></mt-field>
		</div>

		<h3 @click="showSeparateAddress">Separate Use Address </h3>
		<h5> If the Card/Keys are to be used at a different site location (other than the shipping address), please complete the Separate Use Address here. </h5>
		<div v-show="separateAddress">
			<mt-field label="UseContactName" type="text" v-model="editForm.UseContactName"></mt-field>
			<mt-field label="UseCompany" type="text" v-model="editForm.UseCompany"></mt-field>
			<mt-field label="UseAddress1" type="text" v-model="editForm.UseAddress1"></mt-field>
			<mt-field label="UseAddress2" type="text" v-model="editForm.UseAddress2"></mt-field>
			<mt-field label="UseCity" type="text" v-model="editForm.UseCity"></mt-field>
			<mt-field label="UseState" type="text" v-model="editForm.UseState"></mt-field>
			<mt-field label="UseZipcode" type="text" v-model="editForm.UseZipcode"></mt-field>
			<mt-field label="UsePhone" type="text" v-model="editForm.UsePhone"></mt-field>
		</div>

		<div>
			<mt-radio title="Payment Method" v-model="payMethod" :options="payMethodList"></mt-radio>
			<div v-if="payMethodList.length <= 1">
				<p>PO option is not available:</p>
				<p>Thank you for using the Datawatch Systems CardShop On-Line Ordering.  Your account is currently on hold within the CardShop system.  You will not be able to continue with your order until the hold has been removed.  Please contact Chris Neil at <a href= 'mailto:AR@datawatchsystems.com' >AR@datawatchsystems.com</a> or by phone at 301-280-4372 and we will be happy to assist you with your account.</p>
				<p>Your business is very important to us; thanks again for using the Datawatch Systems CardShop On-Line Ordering.</p>
			</div>
		</div>

		<div>

		</div>
			<mt-radio title="Shipping" v-model="shipMethod" :options="shipMethodList"></mt-radio>
			<mt-field v-show="showShipFed_X" label="titleShipFed_X" placeholder="Please Enter Fed-Ex Acct #" type="text" v-model="shipFed_X"></mt-field>
		<div>
			<p>Attention!</p>
			<p>Orders placed AFTER 3:00PM (EST) will be processed the next business day. Orders shipped via regular mail cannot be pre-activated for security reasons.</p>
		</div>
		<div>
			<mt-button @click.native="editItemCancel">Cancel</mt-button>
			<mt-button type="primary" @click.native="editItemSubmit">Save</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Utils from '../utils/index.js';
import BizLogin from '../api/BizLogin.js'
import Order from '../api/Order.js';
import BizOrder from '../api/BizOrder.js'
import TaxRate from '../api/TaxRate.js';
import ShippingRate from '../api/ShippingRate.js';
import Customer from '../api/Customer.js';

import ComboPicker from '../components/ComboPicker.vue';

export default {
	name: 'PayShipping',
	components:{
		'c-picker': ComboPicker
	},
	data(){
		return {
			editForm: {
				ShipState: '',
				ShipCounty: ''
			},
			separateAddress: false,
			stateList:[],
			countyList:[],
			payMethodList:[],
			shipMethodList:[],
			payMethod: '0',
			shipMethod: '',
			shipFed_X: ''
		}
	},
	computed:{
		showShipFed_X()
		{
			let shipMethodObj = this.getShipMethodObj();
			if(shipMethodObj == null)
			{
				return false;
			}
			return shipMethodObj.PrePaidTitle.length > 0
		},
		titleShipFed_X()
		{
			let shipMethodObj = this.getShipMethodObj();
			if(shipMethodObj == null)
			{
				return 'Fed-Ex Acct #';
			}
			return shipMethodObj.PrePaidTitle;
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		showSeparateAddress()
		{
			this.separateAddress = !this.separateAddress;
		},
		onStateChanged(obj)
		{
			this.editForm.ShipState = obj.ShortName;
			this.changeCounty();
		},
		onCountyChanged(obj)
		{
			this.editForm.ShipCounty = obj.county;
		},
		changeCounty()
		{
			//console.log('load county...,' + this.editForm.ShipState + ';' + this.editForm.ShipZipcode);
			//this.countyList.length = 0;
			this.countyList = [];
			for(let i=0; i< this.stateList.length;i++)
			{
				let state = this.stateList[i];
				if(state.ShortName == this.editForm.ShipState && state.Zipcode == this.editForm.ShipZipcode)
				{
					this.countyList.push({county: state.County});
				}
			}
		},
		getShipMethodObj()
		{
			for(let i=0; i< this.shipMethodList.length;i++)
			{
				if(this.shipMethodList[i].ShippingRatePK == this.shipMethod)
				{
					return this.shipMethodList[i];
				}
			}

			return null;
		},
		saveOrder(entity)
		{
			entity.SubTotalAmount = 0;
			for(let i = 0; i< entity.OrderItemList.length; i++)
			{
				let orderItem = entity.OrderItemList[i];
                entity.SubTotalAmount += orderItem.Quantity * orderItem.PaidPrice;
			}
			entity.TaxAmount = entity.SubTotalAmount * entity.TaxRate;

			Order.Save(entity).then((res) => {
				Indicator.close();
				if(res.data.OK){
					this.$store.commit('setCart', entity);
					if(this.payMethod == '0')
					{
						this.$router.push({ name: 'PayBilling' });
					}
					else
					{
						this.$router.push({ name: 'PayMonthly' });
					}			
				}
				else
				{
					this.$message({message: res.data.Error,type: 'error'});
					console.log(res.data);
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});
		},
		editItemCancel()
		{
			this.$router.push({ name: 'Dashboard' });
		},
		editItemSubmit() {
			let shipMethodObj = this.getShipMethodObj();
			if(shipMethodObj == null)
			{
				return;
			}

			Indicator.open();
			let entity = Object.assign({}, this.editForm);
			entity.IsDirty = true;
			entity.IsNew = false;
			entity.IsDeleted = false;

			let customer = Utils.getCustomer();
			entity.ModifiedBy = customer.Email;

			entity.ShippingMethod = shipMethodObj.ShippingType;
			entity.ShippingAmount = shipMethodObj.Rate;
			if(shipMethodObj.PrePaidTitle.length > 0)
			{
				entity.Fed_X = this.shipFed_X;
			}
			else
			{
				entity.Fed_X = '';
			}

			Customer.IsTaxExemptByPK(entity.CustomerPK).then((res) => {
				if(res.data){
					entity.TaxRate = 0;
					entity.IsTaxExempt = true;
					this.saveOrder(entity);
				}
				else
				{
					TaxRate.GetTaxRate(entity.ShipState, entity.ShipZipcode,entity.ShipCounty).then((res) => {
						if(res.data && res.data.Rate > 0){
							entity.TaxRate = res.data.Rate;
							if(res.data.TaxOnShipping)
							{
								entity.ShippingAmount = entity.ShippingAmount * (1 + entity.TaxRate);
							}
						}
						else
						{
							entity.TaxRate = 0;
						}
						entity.IsTaxExempt = false;
						
						this.saveOrder(entity);
					}).catch(err=>{
						Indicator.close();
						this.showNetworkError();
						console.log(err);
					});
				}
			}).catch(err=>{
				Indicator.close();
				this.showNetworkError();
				console.log(err);
			});	
		},
	},
	mounted()
	{
		let entity = this.$store.state.cart;
		if(Object.keys(entity).length == 0)
		{
			this.$router.push({ name: 'Dashboard' });
			return;
		}
		this.editForm = Object.assign({}, entity);

		//State
		this.stateList = this.$store.state.stateList;
		if(this.stateList.length == 0)
		{
			TaxRate.GetAllState().then((res) => {
				this.stateList = res.data;
				this.changeCounty();
				this.$store.commit('setStateList', this.stateList);
			}).catch(err=>{
				console.log(err);
			});
		}
		else
		{
			this.changeCounty();
		}

		//pay methods
		this.payMethodList = this.$store.state.payMethodList;
		if(this.payMethodList.length == 0)
		{
			BizOrder.CanPayByPO(entity).then((res) => {
				if(res.data)
				{
					let payByCC = {};
					payByCC.label = 'Pay by Credit Card';
					payByCC.value = '0';
					this.payMethodList.push(payByCC);

					let payByPO = {};
					payByPO.label = 'Pay by Purchase Order';
					payByPO.value = '1';
					this.payMethodList.push(payByPO);
				}
				else
				{
					let payByCC = {};
					payByCC.label = 'Pay by Credit Card';
					payByCC.value = '0';
					this.payMethodList.push(payByCC);
				}
				this.$store.commit('setPayMethodList', this.payMethodList);
			}).catch(err=>{
				console.log(err);
			});
		}

		//ship methods
		this.shipMethodList = this.$store.state.shipMethodList;
		if(this.shipMethodList.length == 0)
		{
			ShippingRate.GetAll().then((res) => {
				for(let i=0; i< res.data.length; i++)
				{
					let item = res.data[i];
					this.shipMethodList.push(Object.assign({}, item, {label: item.ShippingType + '($' + item.Rate + ')', value: item.ShippingRatePK}));
				}
				if(!this.shipMethod && this.shipMethodList.length > 0)
				{
					this.shipMethod = this.shipMethodList[0].ShippingRatePK;
				}
				this.$store.commit('setShipMethodList', this.shipMethodList);
			}).catch(err=>{
				console.log(err);
			});
		}
		else
		{
			if(!this.shipMethod && this.shipMethodList.length > 0)
			{
				this.shipMethod = this.shipMethodList[0].ShippingRatePK;
			}
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
